<!--suppress HtmlUnknownTag, HtmlUnknownAttribute, CheckEmptyScriptTag -->
<template>
  <v-container fluid>
    <h2>Combined bus timetables</h2>

    <v-expansion-panels v-for="place in Object.keys(timetables)" >
      <v-expansion-panel hover focusable inset >
        <v-expansion-panel-header>
          <h4>{{ place }}</h4>
        </v-expansion-panel-header>

        <v-expansion-panel-content>
          <v-row dense >
            <v-col cols="12" md="4"
                   v-for="memo in timetables[place]" >
              <iframe width="100%" height="333px"
                      :src="mkUrl(memo)" />
            </v-col>
          </v-row>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </v-container>
</template>

<script>

  const providers = {
    more (stop) {
      return 'https://www.morebus.co.uk/stops/' + stop;
    },
    yellow (stop) {
      return 'https://www.yellowbuses.co.uk/stops/' + stop;
    }
  };

  export default {

    data: () => ({
      timetables: {
        'Station toward Centre' : [
          'more~1290BOB20470',
          'more~1290POX38418',
          'yellow~1290BOB20470'
        ],
        'South toward North' : [
          'more~1290BOB20576',
          'more~1290BOB20498',
          'more~1290BOB20407',
          'yellow~1290BOB20498',
          'yellow~1290BOB20404'
        ],
        'Winton toward South' : [
          'yellow~1290DOB21063',
          'more~1290DOB21063',
          'more~1290DOB20963',
          'yellow~1290DOB20963'
        ],
        'Littledown Centre' : [
          'more~1290DOB21183',
          'yellow~1290DOB21183'
        ]
      }
    }),

    methods: {
      mkUrl(memo) {
        const split = memo.match('(.+)~(.+)');
        return providers[split[1]](split[2]);
      }
    }
  }

</script>
